@import "styles/color.scss";

.root {
  display: flex;

  .tiles {
    @media only screen and (max-width: 1000px) {
      flex-direction: row;
    }

    .task-tray {
      flex: 1;

      .list-item {
        display: flex;
        margin-bottom: 10px;

        .details {
          flex: 1;
          display: flex;
          flex-direction: column;

          .detail-item {
            display: flex;

            .label {
              font-weight: bold;
              color: $color-11;
            }

            .content {
              margin-left: 5px;
            }
          }
        }

        .action {
          display: flex;
          align-items: flex-end;
          font-weight: bold;
        }
      }
    }

    .ticket-list {
      // min-width: 370px;
      flex: 1;
    }
  }
}

.client_details {
  margin-top: 15px;
  padding: 30px;
  margin-bottom: 5px;
  .title {
    margin-bottom: 5px;
    margin-top: 15px;
  }
  .subtitle {
    margin-left: 0px;
  }
}

.table_caption {
  max-width: 300px;
}

.pagination {
  text-align: right;
  max-width: 400px;
}

.searchbar {
  padding: "0 0 10px 0";
  minwidth: 1060px;
}

.button {
  background-color: rgb(136, 132, 132);
  display: flex;
  padding: 10px;
  flex: 1;

  .button-positive {
    color: white;
    background-color: #3ab77d;
    border-radius: 0pt;
    margin-left: auto;

    &:hover {
      background-color: #0b4930;
    }
  }

  .button-negative {
    color: white;
    background-color: #353837;
    border-radius: 0pt;

    &:hover {
      background-color: #8b918f;
    }
  }

  .clear {
    clear: both;
  }
}

.customText {
  .MuiTypography-subtitle2 {
    font-size: 0.7rem;
  }
  .MuiTypography-body2 {
  }
}

.buttonWrapper {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;

  .button {
    margin: 10px;
  }
}

.dropdown_option {
  max-width: 300px;
}

.checkbox_option {
  max-width: 300px;
}

.form_right {
  min-width: 850px;
  margin-left: 35px;
  float: 1;
}

.form_left {
  min-width: 440px;
  float: 1;
}

.form_bar {
  margin-top: 500px;
}

.divider {
  min-height: 30px;
}

.form_divider {
  min-height: 30px;
}

.footer_divider {
  min-height: 100px;
}

.tabs {
  margin-left: 20px;
}

.showInfo {
  max-width: 500px;
  .content-header-details {
    display: flex;
    margin-top: 3px;

    .fields {
      max-width: 200px;
      text-align: left;
      flex: 1;
      margin-right: 20px;
    }

    .values {
      text-align: left;
    }
  }
}
