.root {
  .container {
    display: flex;
    padding: 9px 20px 9px 30px;

    .content-header-details {
      margin-right: auto;
    }

    .content-header-summary {
      .summary-wrapper {
        .summary-header {
          background-color: #f5f7fb;
          padding: 1px;
        }

        .summary-content {
          padding: 5px 15px 5px 0px;
          border-left: 1px solid #dddfe3;
          border-right: 1px solid #dddfe3;
          border-bottom: 1px solid #dddfe3;
          .top {
            display: flex;

            .fields {
              width: 100px;
              flex: 1;
              padding: 0px 5px 0px 0px;
              text-align: right;
            }

            .values {
              width: 140px;
              padding: 0px 0px 0px 5px;
              text-align: right;

              .php-text {
                float: left;
                margin-left: 45px;
              }

              .value {
                float: right;
              }
            }
          }

          .bottom {
            display: flex;
            justify-content: center;
            clear: both;

            .fields {
              width: 150px;
              text-align: right;
            }

            .values {
              width: 140px;
              text-align: right;
            }
          }
        }
      }
    }
  }
}
