.remove-icon {
  font-size: 16px;
  color: #fd5451;
  margin-left: 8px;
  margin-bottom: -3px;
}

.drawer-content {
  margin: 20px 0px 20px 0px;
}

.main-content-wrapper {
  padding: 40px;

  .transaction-type-container {
    width: 400px;
    display: flex;

    .transaction-type-field {
      flex: 1;
    }

    .transaction-type-value {
      margin-top: -20px;

      .transaction-type-button {
        background-color: #ffffff;
        text-transform: none;
        font-size: 14px;
        padding: 5px 30px 5px 30px;
        font-weight: 400;
        width: 230px;
      }

      .transaction-type-item-icon-invoice {
        font-size: 18px;
        margin-bottom: -3px;
        color: #1ab1c4;
      }

      .transaction-type-item-icon-credit-memo {
        font-size: 18px;
        margin-bottom: -3px;
        color: #fab435;
      }

      .transaction-type-item-icon-debit-note {
        font-size: 18px;
        margin-bottom: -3px;
        color: #fd5451;
      }

      .transaction-type-item-icon-credit-note {
        font-size: 18px;
        margin-bottom: -3px;
        color: #3ab77d;
      }
    }
  }

  .reference-number-container {
    width: 620px;
    display: flex;
    margin-top: 40px;

    .reference-number-field {
      flex: 1;
    }

    .reference-number-value {
      margin-top: -15px;
    }
  }

  .advanced-search-link {
    color: #3ab77d;
    margin-left: 505px;
  }
}

.button-wrapper {
  margin-top: 337px;

  .button {
    padding: 10px 30px 10px 30px;
    background-color: rgb(136, 132, 132);

    .cancel-link {
      margin-left: 30px;
      text-decoration: none;
    }

    .cancel-button {
      color: white;
      background-color: #151c1b;
      padding: 10px 60px 10px 60px;
      text-transform: none;

      &:hover {
        background-color: #9ea3ac;
      }
    }
  }
}
