@import 'styles/color.scss';

$color-12: #dddfe3;

.root_table {
  .cell {
    margin: 0.1em;
    padding: 0.5em;
    font-size: 0.85rem;
  }
}

.headerbar {
  background-color: $color-12;
}

.link {
  text-decoration: none;
}

.collapseButton {
  padding: 0, 0, 0, 0;
  svg {
    font-size: 0.9rem;
  }
}

.sidescrollButton {
  font-size: 0.7rem;
}

.headerCells {
  font-size: 0.7rem;
}

.headerCollapseButton {
  span {
    padding: 0;
  }
  font-size: 0.7rem;
}

.rowCollapseButton {
  font-size: 0.7rem;
}
