.root {
  display: flex;
  width: 100%;
  flex-direction: column;

  .label {
    padding-bottom: 5px;
  }

  .wrapper {
    .read-only-value {
      font-weight: unset;
      padding: 5px;
    }

    .select-container {
      flex: 1;
      display: flex;

      .select {
        padding: 0;
      }

      .input {
        width: 100%;
        .select {
          padding: 10px;
          padding-right: 20px;
          text-align: left;
        }
      }

      // Errors
      &.has-error {
        border: 1px solid red;
        margin-bottom: unset;
      }

      &.disabled {
        background-color: rgb(223, 223, 223);
      }
    }
    span.error {
      padding-top: 1px;
      color: red;
    }
  }
}
