@import 'styles/color.scss';

.root {
  .header-container {
    padding: 15px;
    margin: 0;

    .header {
      background-color: white;
      padding: 10px;

      .title {
        float: left;
        padding: 20px 0px 5px 20px;
      }

      .close-button {
        float: right;
        margin: -10px -10px 0px 0px;
      }

      .clear {
        clear: both;
      }
    }
  }
}
.search-container {
  display: flex;
  margin-top: -10px;
  margin-bottom: 20px;

  .search-field {
    flex: 1;
  }

  .display-settings {
    .display-settings-icon {
      color: #0d5e40;
      margin-right: 5px;
    }
  }
}

.export-icon {
  color: #0d5e40;
  transform: scaleX(-1);
  -moz-transform: scaleX(-1);
  -webkit-transform: scaleX(-1);
  -ms-transform: scaleX(-1);
  margin-bottom: -7px;
  margin-right: 5px;
}
