.root {
  .header-container {
    padding: 0;
    margin: 0;

    .header {
      display: flex;
      align-items: center;
      padding: 10px;

      .title {
        flex: 1;
      }
    }
  }

  .content {
    flex: 1;
    display: flex;
    flex-direction: column;
    overflow: hidden;

    .search {
      margin-bottom: 10px;
    }
  }
}
