@import 'styles/color.scss';

.root {
  overflow-x: unset !important;
  position: relative;

  .loader {
    position: absolute;
  }

  table {
    min-width: 500px;
  }
}

.footer {
  border-bottom: 0.5px solid #cacaca;
  border: unset !important;
  display: flex !important;
  justify-content: flex-end;
  flex: 1;
  padding-top: 4px;

  td {
    border: unset !important;
  }

  .float {
    margin-top: 20px;
    margin-right: 40vw;
  }
}
