@import "styles/color.scss";

.root {
  display: flex;

  .tiles {
    @media only screen and (max-width: 1000px) {
      flex-direction: row;
    }

    .task-tray {
      flex: 1;

      .list-item {
        display: flex;
        margin-bottom: 10px;

        .details {
          flex: 1;
          display: flex;
          flex-direction: column;

          .detail-item {
            display: flex;

            .label {
              font-weight: bold;
              color: $color-11;
            }

            .content {
              margin-left: 5px;
            }
          }
        }

        .action {
          display: flex;
          align-items: flex-end;
          font-weight: bold;
        }
      }
    }

    .ticket-list {
      // min-width: 370px;
      flex: 2;
    }
  }
}

.client_details {
  padding: 20pt;
  margin-bottom: 40px;

  .client-details-wrapper {
    .corporate-name-wrapper {
      .corporate-name-value {
        font-size: 20px;
        font-weight: bold;
      }
    }

    .basic-hmo-information-wrapper {
      display: flex;

      .left-container {
        flex: 1;

        .information-title {
          font-weight: bold;
          margin-top: 10px;
        }

        .basic-information-wrapper {
          display: flex;

          .field {
            flex: 1;

            .text {
              margin: 5px 0px 5px 0px;
            }
          }

          .value {
            flex: 1;

            .text {
              margin: 5px 0px 5px 0px;
              font-weight: 450;
            }
          }
        }
      }

      .center-container {
        flex: 1;
        margin-top: 34px;

        .basic-information-wrapper {
          display: flex;

          .field {
            flex: 1;

            .text {
              margin: 5px 0px 5px 0px;
            }
          }

          .value {
            flex: 1;

            .text {
              margin: 5px 0px 5px 0px;
              font-weight: 450;
            }
          }
        }
      }

      .right-container {
        flex: 1;

        .information-title {
          font-weight: bold;
          margin-top: 10px;
        }

        .hmo-information-wrapper {
          display: flex;

          .field {
            flex: 1;

            .text {
              margin: 5px 0px 5px 0px;
            }
          }

          .value {
            flex: 1;

            .text {
              margin: 5px 0px 5px 0px;
              font-weight: 450;
            }
          }
        }
      }
    }
  }

  .title {
    margin-bottom: 5px;
    margin-top: 10px;
  }
  .subtitle {
    margin-left: 0px;
  }
}

.table_caption {
  max-width: 300px;
}

.pagination {
  text-align: right;
  max-width: 400px;
}

.searchbar {
  padding: "0 0 10px 0";
  minwidth: 1060px;
}

.button {
  background-color: rgb(136, 132, 132);
  display: flex;
  padding: 10px;

  .button-positive {
    color: white;
    background-color: #3ab77d;
    border-radius: 0pt;
    margin-left: auto;

    &:hover {
      background-color: #0b4930;
    }
  }

  .button-negative {
    color: white;
    background-color: #353837;
    border-radius: 0pt;

    &:hover {
      background-color: #8b918f;
    }
  }

  .clear {
    clear: both;
  }
}

.view-more {
  text-transform: none;
  text-decoration: underline;
  font-weight: 600;
  color: #3ab77d;
  font-size: 14px;
}
