.drawer-content {
  margin: 20px 0px 20px 0px;
}

.content-header-details {
  display: flex;

  .fields {
    margin-right: 20px;
    flex: 1;
  }

  .values {
    margin-right: 8px;
  }

  .status {
    color: #fd5451;
  }
}

.export-view-billing-cycle-wrapper {
  display: flex;
  justify-content: flex-end;
  padding: 10px;

  .export-view-content {
    .export-icon {
      color: #3ab77d;
      transform: scaleX(-1);
      -moz-transform: scaleX(-1);
      -webkit-transform: scaleX(-1);
      -ms-transform: scaleX(-1);
      margin-bottom: -7px;
      margin-right: 5px;
    }

    .export-link {
      margin-top: -10px;
      margin-right: 15px;
      color: #3ab77d;
    }

    .view-icon {
      color: #3ab77d;
      margin-bottom: -7px;
      margin-right: 8px;
    }

    .view-link {
      color: #3ab77d;
      margin-right: 20px;
    }
  }
}

.button {
  position: relative;
  background-color: rgb(136, 132, 132);
  padding: 10px 60px 10px 60px;
  display: flex;
  justify-content: flex-end;
  padding: 10px;
  flex: 1;

  .button-positive {
    color: white;
    background-color: #3ab77d;
    justify-content: flex-end;
    border-radius: 0pt;
    margin-left: auto;

    &:hover {
      background-color: #0b4930;
    }
  }

  .button-negative {
    color: white;
    background-color: #353837;
    border-radius: 0pt;

    &:hover {
      background-color: #8b918f;
    }
  }
}
