.root {
  // background-color: paleturquoise;
  width: 100%;
  // height: 500px;
  // size: a4;

  .removeIcon {
    font-size: 'small';
    color: #fd5451;
  }

  .content-header-details {
    display: flex;

    .fields {
      margin-right: 20px;
      flex: 1;
    }

    .values {
      margin-right: 8px;
    }
  }

  .display-settings-wrapper {
    display: flex;
    justify-content: flex-end;
    padding: 8px;

    .display-settings-content {
      .display-settings-icon {
        color: #0d5e40;
        margin-bottom: -7px;
        margin-right: 5px;
      }

      .display-settings-link {
        margin-top: -10px;
        margin-right: 15px;
        color: #0d5e40;
        cursor: pointer;
      }
    }
  }
}
