.root {
  // background-color: palegreen;
  flex-grow: 1;
  padding: spacing(3);
  transition-timing-function: ease;
  transition-duration: 195ms;
  margin-left: -300px;
}

.contentShift {
  transition-timing-function: ease-out;
  transition-duration: 195ms;
  margin-left: 0;
}

.button {
  background-color: rgb(136, 132, 132);
  display: flex;
  padding: 10px;

  .item-right-side {
    border-radius: 3pt;
    margin-left: auto;
  }
  .button-positive {
    color: white;
    background-color: #3ab77d;
    width: 150px;
    &:hover {
      background-color: #0b4930;
    }
  }
  .button-void {
    color: white;
    background-color: #c53a2e;
    width: 150px;
    &:hover {
      background-color: #490b0b;
    }
  }

  .button-negative {
    color: white;
    background-color: #353837;
    border-radius: 3pt;
    width: 300 !important;

    &:hover {
      background-color: #8b918f;
    }
  }

  .clear {
    clear: both;
  }
}
