.root {
  display: flex;
  position: relative;
  background: #525659;

  .controls {
    display: flex;
    justify-content: center;
    align-content: center;
    align-items: center;
    padding: 10px;
    position: absolute;
    min-height: 60px;
    width: 100%;
    z-index: 1;
    background-color: #323639;

    .control {
      margin-left: 5px;
      color: white;
      font-size: 17px;
    }

    .zoom {
      margin-left: 60px;
      color: white;
      font-size: 17px;
    }

    .back {
      margin-left: -5px;
      color: white;
      font-size: 17px;
    }

    .autozoom {
      margin-left: -5px;
      color: white;
      font-size: 12px;
    }

    .fullscreen {
      margin-left: 100px;
      // left: 30%;
      justify-content: flex-end;
      color: white;
      font-size: 12px;
    }
  }

  .pdf {
    display: flex;
    flex: 1;
    justify-content: center;
    padding: 10px;
    padding-top: 70px;
  }
  .page-number {
    color: #dddddd;
    font-weight: 600;
  }
  .file-name {
    color: #dddddd;
    font-weight: 600;
    margin-right: 50px;
  }
}

.pdfDocument {
  visibility: hidden;
}
