.root {
  .header-container {
    padding: 15px;
    margin: 0;

    .header {
      background-color: white;
      padding: 10px;

      .title {
        float: left;
        padding: 20px 0px 5px 20px;
      }

      .close-button {
        float: right;
        margin: -10px -10px 0px 0px;
      }

      .clear {
        clear: both;
      }
    }
  }

  .content-container {
    padding: 15px;

    .search-container {
      display: flex;
      margin-top: -10px;
      margin-bottom: 20px;

      .search-field {
        flex: 1;
      }

      .display-settings {
        .display-settings-icon {
          color: #0d5e40;
          margin-right: 5px;
        }
      }
    }
  }

  .root {
    .header-container {
      position: fixed;
      top: 5%;
      left: 10%;
      padding: 0;
      margin: 0;

      .header {
        display: flex;
        align-items: center;
        padding: 10px;

        .title {
          flex: 1;
        }
      }
    }

    .content {
      flex: 1;
      display: flex;
      flex-direction: column;
      overflow: hidden;

      .search {
        margin-bottom: 10px;
      }
    }
  }

  .count {
    .grids {
      margin: 0px 55px;
    }
    h4 {
      font-weight: normal;
      color: #48b77c;
    }
    h6 {
      font-weight: bold;
      font-size: 15px;
    }

    .smtext {
      margin-top: -5px;
      font-size: 10px;
      text-align: left;
    }
  }
}
