.advanced-search-link {
  float: right;
  padding-right: 15px;
  color: #3ab77d;
}

.button-wrapper {
  margin-top: 530px;

  .button {
    padding: 10px 30px 10px 30px;
    background-color: rgb(136, 132, 132);

    .cancel-link {
      margin-left: 30px;
      text-decoration: none;
    }

    .cancel-button {
      color: white;
      background-color: #151c1b;
      padding: 10px 60px 10px 60px;
      text-transform: none;

      &:hover {
        background-color: #9ea3ac;
      }
    }
  }
}

