.button {
  display: flex;
  padding: 10px;

  .button-positive {
    color: white;
    background-color: #3ab77d;
    margin-left: auto;

    &:hover {
      background-color: #0b4930;
    }
  }

  .button-negative {
    color: #3ab77d;
    border-color: #3ab77d;

    &:hover {
      background-color: #3ab77d;
      border-color: white;
      color: white;
    }
  }

  .clear {
    clear: both;
  }
}
