.button {
  background-color: rgb(136, 132, 132);
  display: flex;
  padding: 10px;

  .button-positive {
    color: white;
    background-color: #3ab77d;
    border-radius: 0pt;
    margin-left: auto;

    &:hover {
      background-color: #0b4930;
    }
  }

  .button-negative {
    color: white;
    background-color: #353837;
    border-radius: 0pt;

    &:hover {
      background-color: #8b918f;
    }
  }

  .clear {
    clear: both;
  }
}

.view-more {
  text-transform: none;
  text-decoration: underline;
  font-weight: 600;
  color: #3ab77d;
  font-size: 14px;
}
