.root {
  .drawer-content {
    margin: 20px 0px 20px 0px;
  }

  th {
    font-weight: normal !important;
    background-color: lightgray !important;
  }

  tr > td {
    background-color: white !important;
    margin-top: 0px !important;
    margin-bottom: 0px !important;
    padding-top: 10px !important;
    padding-bottom: 10px !important;
  }

  tr:hover {
    background-color: rgba(211, 211, 211, 0.438) !important;
  }

  .content-header-details {
    display: flex;

    .fields {
      margin-right: 20px;
      flex: 1;
      width: 170px;
    }

    .values {
      margin-right: 5px;
    }

    .status {
      color: #fd5451;
    }
  }

  .export-view-billing-cycle-wrapper {
    display: flex;
    justify-content: flex-end;
    padding: 10px;

    .export-view-content {
      .export-icon {
        color: #3ab77d;
        transform: scaleX(-1);
        -moz-transform: scaleX(-1);
        -webkit-transform: scaleX(-1);
        -ms-transform: scaleX(-1);
        margin-bottom: -7px;
        margin-right: 5px;
      }

      .export-link {
        margin-top: -10px;
        margin-right: 15px;
        color: #3ab77d;
      }

      .view-icon {
        color: #3ab77d;
        margin-bottom: -7px;
        margin-right: 8px;
      }

      .view-link {
        color: #3ab77d;
        margin-right: 20px;
      }
    }
  }
  .button-tr {
    .td {
      padding: 0;
      padding-bottom: 15px;
    }
  }
  .button-wrapper {
    display: inline;
    .button {
      display: flex;
      justify-content: flex-end;
      padding: 10px 30px 10px 30px;
      background-color: rgb(136, 132, 132);

      .close-button {
        color: white;
        background-color: #3ab77d;
        padding: 10px 60px 10px 60px;
        text-transform: none;

        &:hover {
          background-color: #0d5e40;
        }
      }
    }
  }
}
