.remove-icon {
  font-size: 16px;
  color: #fd5451;
  margin-left: 8px;
  margin-bottom: -3px;
}

.drawer-content {
  margin: 20px 0px 20px 0px;
}

.export-modify-billing-cycle-wrapper {
  display: flex;
  justify-content: flex-end;
  padding: 10px;

  .export-modify-content {
    .export-icon {
      color: #3ab77d;
      transform: scaleX(-1);
      -moz-transform: scaleX(-1);
      -webkit-transform: scaleX(-1);
      -ms-transform: scaleX(-1);
      margin-bottom: -7px;
      margin-right: 5px;
    }

    .export-link {
      color: #3ab77d;
      margin-top: -10px;
      margin-right: 15px;
    }

    .modify-billing-cycle-icon {
      color: #3ab77d;
      font-size: 16px;
      margin-top: -10px;
      margin-right: 8px;
    }

    .modify-billing-cycle-link {
      color: #3ab77d;
      margin-right: 20px;
    }
  }
}

.pop-over-wrapper {
  margin-top: 12px;
  margin-left: 20px;

  .popover-container {
    width: 400px;
    height: 160px;
    padding: 20px;

    .pop-over-close-button {
      float: right;
      margin-top: -22px;
      margin-right: -23px;
    }

    .modify-billing-cycle-button {
      background-color: #ffffff;
      text-transform: none;
      font-weight: 400;
      color: rgba(0, 0, 0, 0.54);
      margin-top: 5px;
      border: 1px solid rgba(0, 0, 0, 0.54);

      .modify-billing-cycle-button-icon {
        font-size: 20px;
        margin-bottom: -3px;
      }
    }

    .modify-billing-cycle-menu {
      margin-top: 50px;
    }

    .pop-over-apply-close-button-container {
      display: flex;
      margin-top: 15px;

      .pop-over-close-button-wrapper {
        flex: 1;

        .pop-over-cancel-button {
          color: #ffffff;
          text-transform: none;
          padding: 5px 50px 5px 50px;
          background-color: #fd5451;
        }
      }

      .pop-over-apply-button-wrapper {
        .pop-over-apply-button {
          color: #ffffff;
          text-transform: none;
          padding: 5px 50px 5px 50px;
          background-color: #3ab77d;
        }
      }
    }
  }
}

.content-header-details {
  display: flex;

  .fields {
    margin-right: 20px;
    flex: 1;
  }

  .values {
    margin-right: 8px;
  }

  .status {
    color: red;
  }
}

.button-wrapper {
  margin-top: 185px;

  .button {
    display: flex;
    padding: 10px 30px 10px 30px;
    background-color: rgb(136, 132, 132);

    .cancel-link {
      margin-left: 30px;
      text-decoration: none;
    }

    .cancel-button {
      flex: 1;
      color: white;
      background-color: #151c1b;
      padding: 10px 60px 10px 60px;
      text-transform: none;

      &:hover {
        background-color: #9ea3ac;
      }
    }

    .submit-button {
      color: white;
      background-color: #3ab77d;
      padding: 10px 60px 10px 60px;
      text-transform: none;
      margin-left: auto;
      margin-right: 30px;

      &:hover {
        background-color: #0b4930;
      }
    }

    .clear {
      clear: both;
    }
  }
}

