@import 'styles/color.scss';

.root {
  .text-fields {
    margin-bottom: 30px;
    padding-top: 20px;

    .sub-container {
      padding: 20px;

      .input-container {
        padding: 0 12px 0 12px !important;
      }
      .ellipsis-container {
        position: relative;
        margin-top: 30px;
        left: 98%;
        right: 20px;
        width: 50px;
        height: 40px;
      }
    }
  }

  .table-container {
    width: 100%;

    .headers {
      display: flex;
      padding: 0 15px;
      padding-top: 24px;

      .header {
        flex: 1;
        font-weight: 600;
        color: #212a71;
      }
    }
  }

  .dialog-container {
    width: 900px;
    height: 1000px;
  }
}

.buttonWrapper {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;

  .button {
    margin: 10px;
  }
}
