@import "styles/color.scss";

.root {
  display: grid;
  .header {
    indicator {
      background-color: green;
    }

    .item {
      margin-right: 15px;
      margin-left: 0px;
      color: $color-11;
      cursor: pointer;
      &:hover,
      &.active {
        color: #3ab77d;
        text-decoration: none;
      }
    }
  }
  .divider {
    background: #212a71;
    height: 2px;
  }
  .panels {
    width: 100%;
  }
}
