.root {
  .header-container {
    position: fixed;
    top: 5%;
    left: 10%;
    padding: 0;
    margin: 0;

    .header {
      display: flex;
      align-items: center;
      padding: 10px;

      .title {
        flex: 1;
      }
    }
  }

  .content {
    flex: 1;
    display: flex;
    flex-direction: column;
    overflow: hidden;

    .search {
      margin-bottom: 10px;
    }
  }
}

.count {
  position: fixed;
  top: 15%;
  left: 2%;
  .grids {
    margin-left: 150px;
  }
  h5 {
    font-weight: bold;
    color: green;
  }
  h6 {
    font-weight: bold;
  }
}
