.root {
  .header-container {
    padding: 0;
    margin: 0;

    .header {
      // display: flex;
      align-items: center;
      padding: 10px;

      .button {
        float: right !important;
        // flex-direction: row;
        // flex-wrap: wrap;
      }

      .title {
        // flex-direction: row;
        // flex-wrap: wrap;
        font-weight: bold;
        padding-top: 20px;
        padding-left: 10px;
      }

      .subtitle {
        // flex-direction: row;
        // flex-wrap: wrap;
        font-weight: bold;
        padding-top: 10px;
        padding-left: 8px;
      }
    }
  }

  .content {
    flex: 1;
    display: flex;
    flex-direction: column;
    overflow: hidden;

    .search {
      margin-bottom: 10px;
    }
  }

  .button {
    display: flex;
    padding-top: 20px;

    .button-positive {
      color: white;
      background-color: #3ab77d;
      margin-left: auto;

      &:hover {
        background-color: #0b4930;
      }
    }

    .button-negative {
      color: #3ab77d;
      border-color: #3ab77d;

      &:hover {
        background-color: #3ab77d;
        border-color: white;
        color: white;
      }
    }

    .clear {
      clear: both;
    }
  }
}
