@import 'styles/color.scss';

.root {
  display: flex;

  .tiles {
    @media only screen and (max-width: 1000px) {
      flex-direction: row;
    }

    .task-tray {
      flex: 1;

      .list-item {
        display: flex;
        margin-bottom: 10px;

        .details {
          flex: 1;
          display: flex;
          flex-direction: column;

          .detail-item {
            display: flex;

            .label {
              font-weight: bold;
              color: $color-11;
            }

            .content {
              margin-left: 5px;
            }
          }
        }

        .action {
          display: flex;
          align-items: flex-end;
          font-weight: bold;
        }
      }
    }

    .ticket-list {
      // min-width: 370px;
      flex: 2;
    }
  }
}
.searchBar {
  padding: "0 0 10px 0";
  minwidth: 1060px;
}

.table_caption {
  float: left;
  width: 300px;
  padding: 16px;
}

.table_pagination {
  float: right;
  width: 400px;
}

.pagination {
  max-width: 400px;
}
