.root {
  display: flex;
  width: 100%;
  flex-direction: column;

  .label {
    padding-bottom: 5px;
  }

  .wrapper {
    // display: flex;
    // flex-direction: row;

    .read-only-value {
      font-weight: unset;
      padding: 5px;
      min-height: 31px;
    }

    .text-field {
      flex: 1;
      display: flex;

      .input {
        width: 100%;
        input {
          padding: 10px;
          text-align: right;
        }
      }

      .currency {
        flex: 1;
        display: flex;
        justify-content: center;
        align-items: center;
        min-width: 40px;
        border-radius: 0 4px 4px 0;
        background-color: #c0c0c0;

        .label {
          padding: 0;
        }
      }

      border: 1px solid transparent;

      &.focus {
        border-color: green;
      }

      // Errors
      &.has-error {
        border-color: red;
        margin-bottom: unset;
      }
    }
    span.error {
      padding-top: 1px;
      color: red;
    }
  }
}
